.form-deal {
    width: 680px;
    background-color: #FFF;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 2px;
    padding: 30px 20px;
    margin: 0 auto;
}

.form-deal strong {
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}

.form-deal form {
    margin-top: 30px;
}

.form-deal form .input-block + .input-block {
    margin-top: 20px;
}

.form-deal form .input-group {
    margin-top: 20px;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
}

.form-deal form .input-group .input-block {
    margin: 0;
}

.form-deal form .input-block label {
    color: #ACACAC;
    font-size: 14px;
    font-weight: bold;
    display: block;
}

.form-deal form .input-block input {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #EEE;
}

.form-deal form .input-block textarea {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #EEE;
}

.form-deal form button[type=submit] {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background: #7d40e7;
    border-radius: 2px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #FFF;
    cursor: pointer;
    transition: background 0.5s;
}

.form-deal form button[type=submit]:hover {
    background:#6931ca;
}