@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root {
    height: 100%;
}

body {
    background-color: #E5E6F0;
    -webkit-font-smoothing: antialiased;
}

body, input, button {
    font-family: Roboto, sans-serif;
}
main {
    flex: 1 1;
}

main ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    list-style: none;
}

@media (max-width: 650px) {
    main ul {
        grid-template-columns: 1fr;
    }
}


.form-deal-title strong {
    font-size: 24px;
}

.table-deals {
    margin-top: 20px;
    margin-bottom: 40px;
}

li.deal-item {
    background: #fff;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 2px;
    padding: 20px;
}

li.deal-item header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

li.deal-item header img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.deal-item header .user-info {
    margin-left: 10px;
}

.user-info strong {
    display: block;
    font-size: 16px;
    color: #333;
}

.user-info span {
    font-size: 13px;
    color: #999;
    margin-top: 2px; 
}

li.deal-item p {
    color: #666;
    font-size: 14px;
    line-height: 20px;
    margin: 10px 0;
}

li.deal-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
}

li.deal-item a:hover {
    color: #5a2ea6;
}
.form-deal {
    width: 680px;
    background-color: #FFF;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 2px;
    padding: 30px 20px;
    margin: 0 auto;
}

.form-deal strong {
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}

.form-deal form {
    margin-top: 30px;
}

.form-deal form .input-block + .input-block {
    margin-top: 20px;
}

.form-deal form .input-group {
    margin-top: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
}

.form-deal form .input-group .input-block {
    margin: 0;
}

.form-deal form .input-block label {
    color: #ACACAC;
    font-size: 14px;
    font-weight: bold;
    display: block;
}

.form-deal form .input-block input {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #EEE;
}

.form-deal form .input-block textarea {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #EEE;
}

.form-deal form button[type=submit] {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background: #7d40e7;
    border-radius: 2px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #FFF;
    cursor: pointer;
    -webkit-transition: background 0.5s;
    transition: background 0.5s;
}

.form-deal form button[type=submit]:hover {
    background:#6931ca;
}
.form-deal-title strong {
    font-size: 24px;
}

